








































































































import Vue from 'vue';
import Person from '@/data/person';
import iconConfig from '@/store/misc/icons';

import { peopleModule } from '@/store/modules/people';
import { acModule } from '@/store/modules/acs';
import AppreciationCard from '../data/ac';
import { APPRECIATION_CARD_STATE } from '../interfaces/iac';
import List from '@/components/List.vue';

export default Vue.extend({
  name: 'Contacts',
  components: {
    List
  },
  data() {
    return {
      iconConfig,
      APPRECIATION_CARD_STATE,
      selected: undefined as Person | undefined
    }
  },
  computed: {
    people(): Array<Person> {
      const people = peopleModule.allPeople;
      return [...people].sort((a, b) => {
        if (a.name < b.name) return -1;
        if (a.name > b.name) return 1;
        return 0;
      }) || [];
    },
    acs(): Array<AppreciationCard> {
      return acModule.allACs;
    }
  },
  methods: {
    timeString(timestamp: number): string {
      const d: Date = new Date(timestamp);
      return d.toDateString();
    },
    imageSource(person: Person): string {
      return person.image ? require(`@/assets/${person.image.path}`) : '';
    },
    matchingACs(person: Person, state: APPRECIATION_CARD_STATE) {
      return this.acs.filter(ac => (ac.receiver.email === person.email || ac.sender.email === person.email) && ac.state === state);
    },
    acsOfPerson(person: Person) {
      return this.acs.filter(ac => (ac.receiver.email === person.email || ac.sender.email === person.email) && (ac.state === APPRECIATION_CARD_STATE.RECEIVED || ac.state === APPRECIATION_CARD_STATE.SENT));
    }
  },
  watch: {
    selected(newSel, oldSel) {
      if (newSel instanceof Person) return;
      if (newSel === undefined) return;
      this.selected = oldSel;
    }
  }
});
