




































































































import Vue from 'vue'
import AppreciationCard from '@/data/ac';
import Person from '@/data/person';
import Card from '@/components/Card.vue'
import iconConfig from '@/store/misc/icons';
import { Route } from 'vue-router';
import { acModule } from '../store/modules/acs';
import { APPRECIATION_CARD_STATE } from '../interfaces/iac';

export default Vue.extend({
  name: 'List',
  components: {
    Card
  },
  props: {
    acs: Array as () => Array<AppreciationCard>
  },
  data() {
    return {
      active: undefined as AppreciationCard | undefined,
      iconConfig,
      archiveLoading: false as boolean,
      archiveMessage: '' as string,
      archiveDialog: false as boolean,
    }
  },
  methods: {
    imageSource(person: Person): string {
      return person.image ? require(`@/assets/${person.image.path}`) : '';
    },
    archiveAC(): void {
      this.archiveLoading = true;
      if (this.active === undefined) return;
      acModule.archiveAC(this.active)
      .then((result) => {
        if (!result) {
          this.archiveLoading = false;
          this.archiveDialog = false;
          this.archiveMessage = 'Something went wrong while archiving the Appreciation Card.'
          return;
        }
        
        this.archiveLoading = false;
        this.archiveDialog = false;
        this.active = undefined;
      })
    },
    dateString(ac: AppreciationCard): string {
      return ac.state === APPRECIATION_CARD_STATE.RECEIVED 
        ? 'Received on ' + new Date(ac.dateSent).toDateString()
        : 'Sent on ' + new Date(ac.dateSent).toDateString();
    },
    senderReceiver(ac: AppreciationCard): Person {
      return ac.state === APPRECIATION_CARD_STATE.RECEIVED 
        ? ac.sender
        : ac.receiver;
    },
  },
  computed: {
    isActive(): boolean {
      return !!(this.active);
    }
  },
  beforeRouteLeave(to: Route, from: Route, next: CallableFunction): void {
    if (this.isActive) {
      this.active = undefined;      
      next(false);
    }
    else {
      next();
    }
    
  }
});
